import requireurl from '../../requist/requist.js'
import Loadings from '../loading/loading.vue'
export default {
	components:{
		Loadings
	},
	data(){
		return {
			LoadingS:false
		}
	},
	methods :{
		wechartlogin(e){
			console.log(e);
			//true 存在  直接登录   false 去绑定
			if(e.data.msg == "false"){
				uni.redirectTo({
				    url: '../index/index'
				});
			}else{
				uni.setStorage({
				    key: 'storage_user',
				    data: e.data.token,
				    success: function () {
					  uni.redirectTo({
						  url: '../today/today'
					  });
				    }
				});
			}
			this.LoadingS = false;
		},
		loginIn(){
			console.log(123)
			const _that = this
			_that.LoadingS = true
			  wx.login({
				success (res) {
				  if (res.code) {
					requireurl.request('u/v1/wx/wxlogin/',{code:res.code},_that.wechartlogin,_that.errlogin)
				  } else {
				    _that.LoadingS = false
					console.log('登录失败！' + res.errMsg)
				  }
				},
				fail(){
				    _that.LoadingS = false
				}
			  })
		},
	},
	mounted() {
		console.log(new Date() + 1)
	}
}
